import React from 'react';
import Card from './Card';


class CardList extends React.Component{
	constructor(){
		super();

		this.state={
			imgArrListNum:0,
			imgArrList:[],
			imgArrListTrue:[],
			imgArrListFiltered:[],
			scroll:0,
			complete:false,
			h:300,


		}
	}

	componentDidUpdate(){
		//console.log("updatecardlist");
//console.log
			if(
			(this.state.h)!==
			(this.props.prop.h)
			){
			this.setState((prevState) => {
				//console.log(prevState.complete)

					return {h:this.props.prop.h};
				}, () => this.reloadImages());

			}


		if(this.props.robots.length===this.state.imgArrList.length&&(this.props.robots.length!==0)&&!this.state.complete)
			this.setState((prevState) => {
				//console.log(prevState.complete)
				if(prevState.complete===false)
					return {complete:true};
				});



			if(this.state.complete){
				//this.props.titoloLoaded();
				let titolo = document.getElementById("titoloTop");
		    titolo.classList.remove("titolo");

			}

	}


	reloadImages = () =>{
		console.log("reloaddo")
		let immagini = document.getElementsByClassName("card");
    for(var counter=0; counter<immagini.length;counter++){
      if(/\d/.test(immagini.item(counter).id)){

				if(this.state.h===300){
					immagini.item(counter).style.height = "300px"
					immagini.item(counter).style.width = "auto"
				}else {
					immagini.item(counter).style.height = "auto"
					immagini.item(counter).style.width = "85%"

				}
      }
    }

























		for (var i5 = 0; i5 < this.state.imgArrList.length; i5++) {
			//this.state.imgArrList[i5].props.prop.h = 10
			//this.state.imgArrList[i5].props.prop.w = 50
			//console.log(this.state.imgArrList[i5].props.prop.h)

		}
		//console.log(this.state.imgArrList[0]);
		//console.log(this.state.imgArrList[i].props.prop.h)
		//console.log(this.state.imgArrList[i].props.prop.w);
	}

	numeri = (valore)=> {


		this.setState(prevState => {

       return {imgArrListNum: prevState.imgArrListNum + 1}

    })
		//console.log(this.state.imgArrList[this.state.imgArrList.length-1].key)
		if(this.state.imgArrListTrue[this.state.imgArrListNum+1]){
		if(this.state.imgArrList[this.state.imgArrList.length-1].key!==
			this.state.imgArrListTrue[this.state.imgArrListNum+1].key){
		this.setState({
			imgArrList: [...this.state.imgArrList, this.state.imgArrListTrue[this.state.imgArrListNum+1]]
		})

	}
		else{
			this.setState({
				imgArrList: [...this.state.imgArrList, this.state.imgArrListTrue[this.state.imgArrListNum+2]]
			})

		}}else{
			this.props.titoloLoaded();
		}



		this.props.changeImages(this.props.searchfield)


	}

	reloadimgArrList =(e, eliminataNr, indexEliminataNr)=>{
		if(e==="add"){
			let foto = (<Card
					numeri={this.numeri}
					click={this.props.click}
					prop={this.props.prop}
					link={this.props.robots[this.props.robots.length - 1].link}
					robots={this.props.robots}
					key={this.props.robots[this.props.robots.length - 1].id}
					id={this.props.robots[this.props.robots.length - 1].id}
					name={this.props.robots[this.props.robots.length - 1].name}
					email={this.props.robots[this.props.robots.length - 1].email}
					tag={this.props.robots[this.props.robots.length - 1].tag}
					/>)
			/*this.state.imgArrList=[]
			this.state.imgArrListNum=0

			this.pushFirstelement();*/
			this.state.imgArrList.splice(0, 0, foto);

		}else {
			this.state.imgArrList.splice(indexEliminataNr, 1);


			}
	}

	/*pushFirstelement = () =>{
		this.state.imgArrList.push(<Card
				numeri={this.numeri}
				click={this.props.click}
				prop={this.props.prop}
				link={this.props.robots[this.props.robots.length - 1].link}
				robots={this.props.robots}
				key={this.props.robots[this.props.robots.length - 1].id}
				id={this.props.robots[this.props.robots.length - 1].id}
				name={this.props.robots[this.props.robots.length - 1].name}
				email={this.props.robots[this.props.robots.length - 1].email}
				tag={this.props.robots[this.props.robots.length - 1].tag}
				/>)
		}*/

	render(){



		let arrayRobotsDesc = [];
		let imgArrList = []

		for (var i = this.props.robots.length - 1; i >= 0; i--) {
			if(i===this.props.robots.length-1&&this.state.imgArrList.length===0)
				this.state.imgArrList.push(<Card
							numeri={this.numeri}
							click={this.props.click}
							prop={this.props.prop}
							link={this.props.robots[i].link}
							robots={this.props.robots}
							key={this.props.robots[i].id}
							id={this.props.robots[i].id}
							name={this.props.robots[i].name}
							email={this.props.robots[i].email}
							tag={this.props.robots[i].tag}
							/>)

			arrayRobotsDesc[this.props.robots.length-1-i]=this.props.robots[i];
			imgArrList.push(<Card
						numeri={this.numeri}
						click={this.props.click}
						prop={this.props.prop}
						link={this.props.robots[i].link}
						robots={this.props.robots}
						key={this.props.robots[i].id}
						id={this.props.robots[i].id}
						name={this.props.robots[i].name}
						email={this.props.robots[i].email}
						tag={this.props.robots[i].tag}
						/>)
		}
		// eslint-disable-next-line
		this.state.imgArrListTrue = imgArrList;

		//console.log(this.props.robots[this.props.robots.length-1])
		//console.log(this.state.imgArrListTrue[0])


		//main
		if(this.props.robots.length>0)
		for (var i7 = 0; i7 <= this.state.imgArrList.length; i7++) {
			if(this.state.imgArrList[i7]!==undefined){
				if(Number(this.props.robots[this.props.robots.length-1-i7].id)<Number(this.state.imgArrList[i7].key)) {
					let indexEliminataNr = i7;
					let eliminataNr = Number(this.props.robots[this.props.robots.length-1-i7].id)+1
					console.log("rimuovi "+eliminataNr)
					this.reloadimgArrList("elimina", eliminataNr, indexEliminataNr);
					console.log(this.props.robots);
					console.log(this.state.imgArrList);

					break
				}else if(
					(Number(this.props.robots[this.props.robots.length-1-i7].id)>Number(this.state.imgArrList[i7].key))
					&&
					(Number(this.state.imgArrList[0].key)!==Number(this.props.robots[this.props.robots.length-1].id)
					)
				){
					console.log("aggiungi "+this.props.robots[this.props.robots.length-1-i7].id)
					console.log(Number(this.props.robots[this.props.robots.length-1-i7].id))
					console.log(Number(this.state.imgArrList[i7].key))
					this.reloadimgArrList("add");
					break
				}
					//else
					//console.log("no")
				}
		}






		return(
			<div  id="galleria">
				{
					this.state.imgArrList
				}

			</div>
			);
	}
}


export default CardList;



